@use "../layout" as *;
@use "../utilities" as *;
@use "../abstract/" as *;

@mixin width {
    width: 95%;
    margin: auto;
}

main {
    img {
        max-width: 100%;
        max-height: 100%;
        overflow: hidden;
        object-fit: cover;
    }
    min-height: 100vh;
}
#hero {
    position: relative;
    height: 100dvh;
    @include positionCenter(gridCenter);
    .hero__intro {
        text-align: center;
        span.h4 {
            font-size: 1.75em;
            font-weight: 300;
        }
        h1, p, button {
            margin-bottom: .5em;
        }
    }
}

.article {
    .fa {
        height: 25px;
        width: 25px;
        border: 1px solid black;
        font-size: 1em;
        color: white;
        background-color: black;
        box-shadow: none;
    }
    margin-block: 1em;
}

.cool-numbers {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
    .number {
        span {
            display: block;
            margin-bottom: .3em;
        }
        span.number__number {
            font-weight: 700;
            font-size: 1.2em
        }
    }
}
.cool-circle {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.circle-oval {
    border: 1px solid black;
    border-radius: 34px;
    -webkit-border-radius: 34px;
    -moz-border-radius: 34px;
    -ms-border-radius: 34px;
    -o-border-radius: 34px;
    width: 300px;
    display: flex;
    gap: 1em;
    align-items: center;
    padding: .3em 1em;
    margin: .2em 0;
}

//

.about {
    //make the box pop out of the screen, need to go to church sorry
    box-shadow: .4em .1em .1em .1em black;
    border: 1px solid black;
    margin: 2em 0;
    // display: flex;
    // justify-content: space-evenly;
    span {
        @include positionCenter(absoluteCenter);
        border: 4px solid black;
        padding: 2em;
    }
    #{&}__img{
        position: relative;
        height: 50vh;
        // max-width: 40vw;
        background: linear-gradient( to top left, red, blue, orange, burlywood);
    }
    #{&}__content{
        line-height: 1.5em;
        padding: .2em 1em;
        p, button {
            margin-bottom: 1em;
        }
    }
}

//

.box-c {
    //responsive grid layout;
    display: grid;
    grid-template-areas: 
    "img1 img1 content"
    "img2 logo img3"
    ;
    #{&}__item {
        border: 1px solid black;
        grid-area: img1;
    }
}
//
#services {
    margin-block: .9em;
}
.services-intro {
    h3, p {
        margin-block: .2em;
    }
    p {
        margin-block: .5em 1.3em;
    }
}

.content-cover {
    display: flex;
    flex-wrap: wrap;
    gap: .5em;
}

.box {
    h3, p, button {
        margin-block: .5em;
        color: black;
    }
    h3 {
        margin-top: 0;
        padding-top: .5em
    }
    .box-content-cover {
        background-color: #ad8f69;
    }
    min-height: 60vh;
    width: 400px;
    // border: 1px solid black;
    margin: auto;
    #{&}__img {
        height: 40vh;
        width: 100%;
        background: linear-gradient(to right, red, pink);
        overflow: hidden;
        img {
            min-width: 100%;
            min-height: 100%;
        }
    }
    #{&}__content {
        width: 90%;
        margin-inline: auto;
    }
    #{&}__button {
        // @extend .box__content;
        width: 90%;
        margin-inline: auto;
    }
}


#message {
    background-color: #22211f;;
}
